      readMore: "é˜…è¯»Crusté€šè¯ç›¸å…³æŒ‡æ ‡èŽ·å–æ›´å¤šä¿¡æ¯ã€‚",
      step1: "æ­¥éª¤1ï¼šä¸ºUniswap CRU Poolsæä¾›æµåŠ¨æ€§",
      step21: "æ­¥éª¤2ï¼šé€‰æ‹©ä¸€ä¸ªäº¤æ˜“æ± å¹¶è´¨æŠ¼LP token",
      step22: "æ­¥éª¤2ï¼šè´¨æŠ¼/å–å‡ºLP Tokenï¼ŒèŽ·å¾—CSMå¥–åŠ±",
